@import "../../global.scss";

.testimonials {
  background-color: white;
  display: flex;
  flex-direction: column;
  align-items: center;

  @include mobile {
    justify-content: space-around;
}

  h1 {
    font-size: 50px;

    @include mobile {
        font-size: 20px;
    }
  }

  .container {
    width: 100%;
    height: 80%;
    display: flex;
    align-items: center;
    justify-content: center;

    @include mobile {
        flex-direction: column;
        height: 90%;
    }

    .card {
      width: 250px;
      height: 70%;
      border-radius: 10px;
      box-shadow: 0px 0px 15px -8px black;
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      padding: 20px;
      transition: all 1s ease;

      @include mobile {
        margin: 10px 0;
        flex-direction: column-reverse;
        text-align: center;
     }

    
    @include tablet {
      padding: 5px;
      justify-content: space-evenly;
  }


      &.featured{
          width: 300px;
          height: 75%;
          margin: 0 30px;

          @include mobile {
            width: 250px;
            height: 80px;
            margin: 1px;
        }
      }

      &:hover {
          transform: scale(1.1)
      }

      .top {
        display: flex;
        align-items: center;
        justify-content: center;

        img {
          &.user {
            height: 160px;
            width: 260px;
            border-radius: 5%;
            object-fit: cover;
            margin: 0 30px;

            @include mobile {
              display: none;
            }

            
            @include tablet {
              width: 130px;
          }
          }
        }
      }

      .center {
        padding: 10px;
        border-radius: 10px;
        background-color: rgb(250, 244, 245);

        @include mobile {
            font-size: 12px;
            padding: 2px;
        }
      }

      .bottom {
        display: flex;
        align-items: center;
        flex-direction: column;
        justify-content: center;

        h3 {
          margin-bottom: 2px;
          font-size: 12px;

          @include mobile {
            font-size: 14;
            }
        }

        h4{
            color: rgb(121, 115, 115);

            @include mobile {
                font-size: 13px;
            }
        }
      }
    }
  }
}