@import "../../global.scss";

.intro {
    background-color: white;
    display: flex;

    @include mobile {
        flex-direction: column;
        align-items: center;
    }

    .left {
        flex: 0.5;
        overflow: hidden;

        @include squirrelPhoneH {
            display: none;
        }

        .imgContainer {
            width: 700px;
            height: 700px;
            background-color: crimson;
            border-radius: 50%;
            display: flex;
            align-items: center;
            justify-content: center;
            float: right;

            @include mobile {
                align-items: flex-start;
            }
        }

        img {
            height: 60%;
            border: 40px solid white;
            border-radius: 15px;

            @include mobile {
                height: 35%;
                border: 10px solid white;
                margin-top: 55px;
            }
        }
    }

    .right {
        flex: 0.5;
        position: relative;

        .wrapper {
            width: 100%;
            height: 100%;
            padding-left: 50px;
            display: flex;
            flex-direction: column;
            justify-content: center;

            @include mobile {
                padding-left: 0;
                align-items: center;
            }

            h1 {
                font-size: 60px;
                margin: 10px 0;

                @include mobile {
                    font-size: 40px;
                }
            }
            h2 {
                font-size: 35px;
            }
            h3 {
                font-size: 30px;

                @include mobile {
                    font-size: 20px;
                }
            }

            span {
                font-size: inherit;
                color: crimson;
            }

            .ityped-cursor {
                animation: blink 1s infinite;
            }

            @keyframes blink {
                50% {
                    opacity: 1;
                }
                100% {
                    opacity: 0;
                }
            }
        }

        a {
            position: absolute;
            bottom: 10px;
            left: 40%;

            img {
                width: 30px;
                animation: arrowBlink 5s infinite;
            
            @include squirrelPhoneH {
            display: none;
        }
            }

            @keyframes arrowBlink {
                100% {
                    opacity: 0;
                }
            }
        }
        p {
            width: 80%;
            margin: 20px 20px 0 0;
            line-height: 1.6;
            font-size: 20px;
            
            @include mobile {
                margin-left: 40px;
                font-size: 14px;
                text-align: center;
            }

            @include squirrelPhoneH {
                padding-top: 10px;
            }
        }
    }
}