$mainColor: #15023a;

$phoneWidth: 768px;

@mixin mobile {
    @media (max-width: #{$phoneWidth}){
        @content
    }
};

$tabletWidth: 1280px;

@mixin tablet {
    @media (max-width: #{$tabletWidth}){
        @content
    }
};

$squirrelPhoneWidth: 360px;

@mixin squirrelPhone {
    @media (max-width: #{$squirrelPhoneWidth}){
        @content
    }
};

@mixin squirrelPhoneH {
    @media (max-width: #{$phoneWidth}){
        @content
    }
}

